import React, { useState, useRef, useEffect } from 'react'
import DatePicker from 'react-date-picker'

function ContainedDatePicker ({ value, onChange }) {
  const [date, setDate] = useState(value)
  const pickerElement = useRef(null)

  return (
    <DatePicker
      onCalendarClose={() => onChange(date)}
      ref={pickerElement}
      format='dd/MM/yyyy'
      style={{ border: 'none' }}
      onChange={setDate}
      value={date}
    />
  )
}
export default ContainedDatePicker
