import React, { Children } from 'react'
import styled from 'styled-components'

const StyledButton = styled.div`
  background-color: ${props => props.enabled ? '#297BE6' : '#999'};
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 0;
  border-radius: 3px;
  cursor: ${props => props.enabled ? 'pointer' : 'initial'};
  transition: 200ms;

  :hover {
    background-color: ${props => props.enabled ? '#6F9DDF' : '#999'};
  }
`

function Button ({ onClick, enabled = true, children }) {
  return (
    <StyledButton onClick={enabled ? onClick : null} enabled={enabled}>
      {children}
    </StyledButton>
  )
}
export default Button
