import React, { useState } from 'react'
import Swatch from './Swatch'
import { TwitterPicker } from 'react-color'

function GradientSelector ({ fillColors, onFillColors }) {
  const [editingFillColorIndex, setEditingFillColorIndex] = useState(0)

  return (
    <>
      <Swatch colors={fillColors} activeIndex={editingFillColorIndex} onClick={(i) => setEditingFillColorIndex(i)} />
      <TwitterPicker
        triangle={editingFillColorIndex ? 'top-right' : 'top-left'}
        colors={['#000', '#B80000', '#DB3E00', '#FCCB00', '#008B02', '#006B76', '#1273DE', '#004DCF', '#5300EB', '#EB9694', '#FAD0C3', '#FEF3BD', '#C1E1C5', '#BEDADC', '#C4DEF6', '#BED3F3', '#D4C4FB']}
        width='100%'
        color={fillColors[editingFillColorIndex]}
        disableAlpha
        onChange={(color) => onFillColors(color, editingFillColorIndex)}
      />
    </>
  )
}
export default GradientSelector
