import React, { useState } from 'react'
import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit, faTrash } from '@fortawesome/free-solid-svg-icons'
import InputHeadline from './InputHeadline'
import Button from './Button'
import Popup from 'react-animated-popup'
import DatePicker from 'react-date-picker'
import Input from './Input'

const Container = styled.div`
  margin: 10px 0;
`

const EventElement = styled.div`
  display: flex;
  align-items: center;
  padding: 6px;
  border: 2px solid #eee;
  border-radius: 2px;
  margin: 6px 0;
`

const Text = styled.p`
  flex: 1;
  margin: 0;
  color: #666;
`

const iconStyle = {
  padding: '0 6'
}

const Hover = styled.div`
  opacity: 0.3;
  transition: 200ms;
  cursor: pointer;

  :hover {
    opacity: 6;
  }
`

const PopupContainer = styled.div`
  display: flex;
  flex-direction: column;
`

function Event ({ event, onEdit, onDelete }) {
  return (
    <EventElement>
      <Text>{event.index}: {event.text}</Text>
      <Hover onClick={() => onEdit(event)}>
        <FontAwesomeIcon size='sm' style={iconStyle} icon={faEdit} />
      </Hover>
      <Hover onClick={() => onDelete(event)}>
        <FontAwesomeIcon size='sm' style={iconStyle} icon={faTrash} />
      </Hover>
    </EventElement>
  )
}

function EventEditor ({ events, onDelete, onAdd }) {
  const [showPopup, setShowPopup] = useState(false)
  const [date, setDate] = useState(new Date())
  const [text, setText] = useState('')
  const [editing, setEditing] = useState()

  const openPopup = () => {
    setShowPopup(true)
  }

  const createEvent = () => {
    setEditing(null)
    setShowPopup(false)
    onAdd({ timeStamp: Math.round(date.getTime() / 1000), text: text })
  }

  const onEdit = (event) => {
    setEditing(event)
    setText(event.text)
    setDate(new Date(event.timeStamp * 1000))
    setShowPopup(true)
    onDelete(event)
  }

  const onClose = () => {
    setShowPopup(false)
    if (editing) {
      onAdd(editing)
      setEditing(null)
    }
  }

  return (
    <Container>
      <InputHeadline>Events</InputHeadline>
      {events.map(event => <Event event={event} onDelete={onDelete} onEdit={onEdit} key={'eventEditor' + event.id} />)}
      <Button onClick={openPopup}>Create new event</Button>
      <Popup visible={showPopup} onClose={onClose}>
        <PopupContainer>
          <p>New event</p>
          <DatePicker
            format='dd/MM/yyyy'
            style={{ border: 'none' }}
            onChange={setDate}
            value={date}
          />
          <Input onChange={(e) => setText(e.target.value)} value={text} />
          <Button onClick={createEvent}>Create event</Button>
        </PopupContainer>
      </Popup>
    </Container>
  )
}
export default EventEditor
