import React, { useEffect, useRef, useState } from 'react'
import styled from 'styled-components'

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
`

const Inner = styled.div`
  position: absolute;
  box-shadow: 0 0 20px #999;
`

function AutoSize ({ children }) {
  const [scale, setScale] = useState(1)
  const containerElement = useRef(null)
  const innerElement = useRef(null)

  const calcScale = () => {
    if (!(containerElement.current && innerElement.current)) { return }
    // console.log('it do go down')
    // console.log(scale * 1000 + 'px')

    const xScale = containerElement.current.scrollWidth / innerElement.current.scrollWidth
    const yScale = containerElement.current.scrollHeight / innerElement.current.scrollHeight

    setScale(xScale < yScale ? xScale : yScale)
  }

  window.addEventListener('resize', () => {
    calcScale()
  })

  // This causes issues for the svg text scaling! Too bad!
  useEffect(() => {
    calcScale()
  })

  return (
    <Container ref={containerElement}>
      <Inner scale={scale} ref={innerElement} style={{ transform: 'scale(' + scale + ')' }}>
        {/* Possible fix using z translation instead... */}
        {/* <Inner scale={scale} ref={innerElement} style={{ transform: 'perspective(100px) translateZ(' + scale * 10 + 'px)' }}> */}

        {children}
        {/* Send back on rendered from child maybe? */}
        {/* {React.cloneElement(children, { id: 'foobar' })} */}
      </Inner>
    </Container>
  )
}
export default AutoSize
