import React, { useState } from 'react'
import { getNiceSpan, dateToString, getPointsFromSize, getIndexFromTimeStamp, cmToPoints, translate } from '../lib/utils'
import ChartLines from './SVG/ChartLines'
import TextBox from './TextBox'
import Titles from './SVG/Titles'
import DateAxis from './SVG/DateAxis'

import Event from './SVG/Event'

const shiftToClosest = (compare, timestamps) => {
  if (timestamps.length === 0) { return compare }

  let smallest = 0
  let res = 0
  for (const timestamp of timestamps) {
    if (smallest === 0) {
      smallest = Math.abs(timestamp - compare)
    } else {
      if (smallest > Math.abs(timestamp - compare)) {
        smallest = Math.abs(timestamp - compare)
        res = timestamp
      }
    }
  }
  return res
}

const Poster = ({ title = '', subTitle = '', symbol = '', companyDescription = '', exchangeName = '', market = '', color, fill = 'none', eventColor = color, showDate = true, startDate, endDate, size, stockData, events, style, onAddEvent, lineThickness }) => {
  const [currentHoverElement, setCurrentHoverElement] = useState(null)
  const width = React.useMemo(() => getPointsFromSize(size)[0] / 72 * 96, [size])
  const height = React.useMemo(() => getPointsFromSize(size)[1] / 72 * 96, [size])

  const [minValue, maxValue] = getNiceSpan(stockData ?? [])

  const chartY = 300
  const chartH = height - chartY - 265

  const chartX = 130
  const chartW = width - chartX - chartX

  const timestamps = React.useMemo(() => {
    if (!stockData) { return [] }
    return stockData.map(stock => stock.timestamp)
  }, [stockData])

  events = React.useMemo(() => {
    return events.map(event => {
      if (timestamps.includes(event.timeStamp)) {
        return event
      } else {
        event.timeStamp = shiftToClosest(event.timeStamp, timestamps)
        return event
      }
    })
  }, [events, stockData])

  // add events...
  const points = React.useMemo(() => {
    if (stockData == null) return []

    return stockData.map((stockObject, index, array) => {
      const value = stockObject.close
      const x = chartX + (index / (array.length - 1)) * chartW
      const y = chartY + chartH - (((value - minValue) / maxValue) * chartH)

      return `${x.toFixed(8)} ${y.toFixed(8)}`
    })
  }, [size, stockData, fill])

  const fillPoints = React.useMemo(() => {
    if (stockData == null) return []

    const xStart = chartX + 0
    const yStart = chartY + chartH - (((0 - minValue) / maxValue) * chartH)
    const xEnd = chartX + ((stockData.length - 1) / (stockData.length - 1)) * chartW
    const yEnd = chartY + chartH - (((0 - minValue) / maxValue) * chartH)
    return [xStart + ' ' + yStart, ...points, xEnd + ' ' + yEnd]
  }, [size, stockData, fill])

  const hoverDot = (x, y) => {
    setCurrentHoverElement(<circle cx={x} cy={y} r='8' stroke='none' fill={color} />)
  }

  return (
    // <svg id='mySVG' viewBox={`0 0 ${width} ${height}`} style={style}> // Viewbox causes issues with font-sizes!
    <svg id='mySVG' width={width} height={height} style={{ backgroundColor: 'green', display: 'block' }}>
      {/* <svg id='mySVG' width={width} height={height} style={{ backgroundColor: 'green', display: 'block' }}> */}
      <defs>
        <linearGradient id='gradient' x1='0%' y1='0%' x2='0%' y2='100%'>
          <stop offset='0%' stopColor={Array.isArray(fill) ? fill[0] : '#fff'} stopOpacity={1} />
          <stop offset='100%' stopColor={Array.isArray(fill) ? fill[1] : '#fff'} stopOpacity={0} />
        </linearGradient>
      </defs>

      {/* Borders */}
      <rect x='0' y='0' width={width} height={height} fill='white' />
      <rect x={cmToPoints(2.5)} y={cmToPoints(2.5)} width={width - cmToPoints(2.5) * 2} height={height - cmToPoints(2.5) * 2} fill='white' stroke='black' />
      <rect x={cmToPoints(3)} y={cmToPoints(3)} width={width - cmToPoints(6)} height={height - cmToPoints(6)} fill='white' stroke='black' strokeWidth='3' />

      {/* Titles */}
      <Titles width={width} title={title} subTitle={subTitle} showDate={showDate} startDate={startDate} endDate={endDate} />

      {/* CHART BG LINES */}
      <ChartLines chartX={chartX} chartY={chartY} chartW={chartW} chartH={chartH} minValue={minValue} maxValue={maxValue} />

      {/* CHART PATH */}
      <polyline fill='none' strokeWidth={lineThickness} stroke={color} points={points.join(' ')} />
      {fill !== 'none' &&
        <polyline fill='url(#gradient)' strokeWidth='0' stroke='none' points={fillPoints.join(' ')} />}
      {/* CURRENT HOVER DOT */}
      {currentHoverElement}
      {/* HOVER DOTS */}
      {points.map((point, i) => <circle key={point} onClick={() => onAddEvent(stockData[i].timestamp)} onMouseLeave={() => setCurrentHoverElement(null)} onMouseEnter={(e) => { hoverDot(point.split(' ')[0], point.split(' ')[1]) }} cx={point.split(' ')[0]} cy={point.split(' ')[1]} r='10' stroke='none' fill='transparent' />)}

      {/* EVENTS */}
      {events.map(event => <Event key={event.id} event={event} color={eventColor} stockData={stockData} points={points} />)}

      {/* <Events stockData={stockData} events={events} chartX={chartX} chartY={chartY} chartH={chartH} chartW={chartW} minValue={minValue} maxValue={maxValue} /> */}

      {events.map((event, i) => <text key={event.id + 'eventText'} x={chartX + chartW - 300 + Math.floor(i / 5) * 160} y={chartY + chartH + 60 + 16 * (i - Math.floor(i / 5) * 5)} fontSize={7}>{i + 1 + '. ' + event.text}</text>)}

      {/* Date axis */}
      {timestamps.length && <DateAxis startStamp={timestamps[0]} endStamp={timestamps[timestamps.length - 1]} chartW={chartW} chartX={chartX} y={chartY + chartH + 25} />}

      {/* COMPANY DESCRIPTION */}
      <TextBox text={companyDescription} width={width - 730} fontSize='7' lineSpacing='12' maxLines={false} x={chartX + 100} y={chartY + chartH + 60} />
      {/* <TextBox text={companyDescription} width={width} fontSize='9' lineSpacing='12' maxLines={false} x={0} y={chartY + chartH + 25} /> */}

      {/* <text x={0} y={chartY + chartH + 25}>I am a very long text that will be rendered incorrectly I presume. I am a very long text that will be rendered incorrectly I presume.</text> */}

      <g transform={translate(chartX, chartY + chartH + 60)}>
        <text fontSize='7' fontWeight='bolder' x={0} y={0}>{symbol}</text>
        <text fontSize='7' fontWeight='bolder' x={0} y={15}>{exchangeName}</text>
        <text fontSize='7' fontWeight='bolder' x={0} y={30}>{market}</text>
      </g>

      {/* <text x={width - chartX - 135} y={chartY + chartH + 40}>Buy: 203 USD</text>
      <text x={width - chartX - 135} y={chartY + chartH + 65}>SELL: 2052 USD</text>
      <text x={width - chartX - 135} y={chartY + chartH + 90}>YIELD: +1050%</text> */}
    </svg>
  )
}

export default Poster
