import React, { useState } from 'react'
import Select from 'react-select'
import { searchStock } from '../lib/api'

function StockSelect ({ value, onStockSelect }) {
  const [options, setOptions] = useState([])

  const updateSearch = (query) => {
    if (!query) { return }

    const fetch = async () => {
      const res = await searchStock(query)
      const newItems = res.quotes.map(quote => { return { value: quote.symbol, label: quote.shortname } })
      setOptions(newItems)
    }
    fetch()
  }

  return (
    <Select
      isSearchable
      value={value}
      onChange={onStockSelect}
      onInputChange={updateSearch}
      options={options}
    />
  )
}

export default StockSelect
