import React, { useEffect, useState } from 'react'
import { pdfjs, Document, Page } from 'react-pdf'
// import { pdf } from '@react-pdf/renderer'
import ClipLoader from 'react-spinners/ClipLoader'
import { svgToPDFURL } from '../lib/utils'

pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`

const Loader = () => {
  return (
    <ClipLoader size={150} color='#123abc' loading />
  )
}

const PDFViewer = ({ children, dimensions, aspectRatio, onLoadSuccess }) => {
  const [pdfUrl, setPdfUrl] = useState(null)

  useEffect(() => {
    const fetch = async () => {
      const child = React.Children.only(children)
      const url = await svgToPDFURL([dimensions.height, dimensions.width], child)
      console.log(url)
      setPdfUrl(url)
    // pdf(child).toBlob().then(blob => {
    // setPdfUrl(URL.createObjectURL(blob))
    // })
    }

    fetch()
  }, [children])

  let width = null
  let height = null

  if (dimensions.width > dimensions.height * aspectRatio) {
    // height is limiting factor
    height = dimensions.height
  } else {
    width = dimensions.width
  }

  return (
    <Document file={pdfUrl} loading={<Loader />} onLoadSuccess={onLoadSuccess} noData={<Loader />}>
      <Page renderMode='canvas' pageNumber={1} scale={1} width={width} height={height} />
    </Document>
  )
}

export default PDFViewer
