import React, { useState } from 'react'
import Popup from 'react-animated-popup'
import Button from './Button'
import Input from './Input'
import styled from 'styled-components'

const Container = styled.div`
  display: flex;
  flex-direction: column;
`

function InputPopup ({ visible, onClose, onSubmit, title, maxCharacters = 9999 }) {
  const [text, setText] = useState('')

  const finish = () => {
    onSubmit(text)
    setText('')
  }

  return (
    <Popup visible={visible} onClose={onClose}>
      <Container>
        <p>{title}</p>
        <Input value={text} onChange={(e) => e.target.value.length < maxCharacters ? setText(e.target.value) : null} placeholder='Event text...' />
        <Button enabled={!!text} onClick={finish}>Add event</Button>
      </Container>
    </Popup>
  )
}
export default InputPopup
