import React from 'react'
import styled from 'styled-components'

const Container = styled.div`
  height: 40px;
  width: 100%;
  border-radius: 5px;
  margin: 20px 0;
  overflow: hidden;
  display: flex;
`

const Color = styled.div`
  background-color: ${props => props.color};
  display: flex;
  flex: 1;
  border: ${props => props.highlighted ? 'solid #1273DE 3px' : 'none'};
`

function Swatch ({ colors, activeIndex = 0, onClick = () => (null) }) {
  // console.log('rendering swatch')

  if (!Array.isArray(colors)) {
    colors = [colors]
  }

  if (colors.length === 1) {
    activeIndex = 2
  }

  return (
    <Container>
      {colors.map((color, i) => <Color onClick={() => onClick(i)} key={'swatch' + color} color={color} highlighted={i === activeIndex} />)}
    </Container>
  )
}
export default Swatch
