import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
// import DatePicker from 'react-date-picker'
import Select from 'react-select'
import { getQuotes, getChart, getProfile } from './lib/api'
import InputHeadline from './components/InputHeadline'
import Poster from './components/Poster'
import StockSelect from './components/StockSelect'
import { dateToString, exportPDF, getPointsFromSize } from './lib/utils'
import Input from './components/Input'
import { TwitterPicker } from 'react-color'
import Checkbox from './components/Checkbox'
import Button from './components/Button'
import AutoSize from './components/AutoSize'
import Swatch from './components/Swatch'
import Slider from 'react-input-slider'
import InputPopup from './components/InputPopup'
import PDFViewer from './components/PDFViewer'
import GradientSelector from './components/GradientSelector'
import EventEditor from './components/EventEditor'
import { v4 as uuidv4 } from 'uuid'
import ContainedDatePicker from './components/ContainedDatePicker'

const sizes = [
  { value: { size: [40, 30], unit: 'cm' }, label: '30x40cm' },
  { value: { size: [70, 50], unit: 'cm' }, label: '50x70cm' },
  { value: { size: [100, 70], unit: 'cm' }, label: '70x100cm' },
  { value: { size: [17, 11], unit: 'inches' }, label: '11x17"' },
  { value: { size: [24, 18], unit: 'inches' }, label: '18x24"' },
  { value: { size: [36, 24], unit: 'inches' }, label: '24x36"' }
]

const intervals = [
  { value: '5m', label: '5m' },
  { value: '15m', label: '15m' },
  { value: '1d', label: '1d' },
  { value: '1wk', label: '1wk' },
  { value: '1mo', label: '1mo' }
]

const Container = styled.div`
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  width: 100vw;

  .react-date-picker__wrapper {
    border: none;
  }

  .react-date-picker__wrapper svg {
    width: 12px;
  }
`

const SideBar = styled.div`
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 300px;
  padding: 20px;
  max-height: calc(100vh - 40px);
  overflow: scroll;

  * {
    flex-shrink: 0;
  }
`

const PosterContainer = styled.div`
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 1px;
  background-color: #f4f4f4;
  padding: 20px;
`

const getStockData = async (symbol, interval, startDate, endDate) => {
  const data = await getChart(symbol, interval, startDate, endDate)
  // window.localStorage.setItem('apple', JSON.stringify(data))
  // const data = JSON.parse(window.localStorage.getItem('apple'))
  // console.log(data)
  return (data)
}

const getProfileData = async (symbol) => {
  const data = await getProfile(symbol)
  // console.log('here is the data')
  // console.log(data)
  // window.localStorage.setItem('appleProfile', JSON.stringify(data))
  // const data = JSON.parse(window.localStorage.getItem('appleProfile'))

  // console.log(data)

  return (data)
}

function App () {
  const [stockSymbol, setStockSymbol] = useState({ value: 'AAPL', label: 'Apple Inc' })
  const [title, setTitle] = useState('Your title')
  const [subTitle, setSubtitle] = useState('Your subtitle')
  const [companyDescription, setCompanyDescription] = useState('')
  const [exchangeName, setExchangeName] = useState('')
  const [market, setMarket] = useState('')
  const [startDate, setStartDate] = useState(new Date('2007-01-01T01:00:00'))
  const [endDate, setEndDate] = useState(new Date())
  const [stockData, setStockData] = useState(null)
  const [size, setSize] = useState(sizes[0])
  const [interval, setInterval] = useState(intervals[1])
  const [lineThickness, setLineThickness] = useState(1)
  const [color, setColor] = useState('#FCCB00')
  const [eventColor, setEventColor] = useState(true)
  const [fill, setFill] = useState(false)
  const [fillColors, setFillColors] = useState(['#FEF3BD', '#FCCB00'])
  const [showDate, setShowDate] = useState(true)
  const [currentlyAddingEvent, setCurrentlyAddingEvent] = useState(false)
  const [events, setEvents] = useState([]) // [{timestamp: 1, text: 'foo'}]

  useEffect(() => {
    if (!stockSymbol) { return }
    const fetch = async () => {
      console.log('fetching stock')
      const stock = await getStockData(stockSymbol.value, interval.value, startDate, endDate)
      setStockData(stock)

      // use this line to debug events
      // setEvents([{ timeStamp: 1599053400, text: 'Fam pork', index: 1, id: 'foo' }, { timeStamp: 1599658200, text: 'Vinyl pinterest', index: 2, id: 'foo2' }, { timeStamp: 1600176600, text: 'XOXO schlitz', index: 3, id: 'foo3' }, { timeStamp: 1600435800, text: 'Vape shaman', index: 4, id: 'foo4' }, { timeStamp: 1601559000, text: 'Poke synth', index: 5, id: 'foo5' }, { timeStamp: 1602250200, text: 'Pour-over', index: 6, id: 'foo6' }, { timeStamp: 1602595800, text: 'Next level', index: 7, id: 'foo7' }, { timeStamp: 1603805400, text: 'taiyaki truffaut', index: 8, id: 'foo8' }, { timeStamp: 1603891800, text: 'Bushwick green', index: 9, id: 'foo9' }, { timeStamp: 1603978200, text: 'Tumblr flexitarian ', index: 10, id: 'foo10' }])
    }
    fetch()
  }, [stockSymbol, interval, startDate, endDate])

  useEffect(() => {
    if (!stockSymbol) { return }
    const fetch = async () => {
      console.log('fetching profile')
      const profile = await getProfileData(stockSymbol.value)
      setExchangeName(profile.price.exchangeName)
      setMarket(profile.quoteType.market)
      setCompanyDescription(profile.assetProfile.longBusinessSummary)
    }
    fetch()
  }, [stockSymbol])

  const addEvent = (eventText, timeStamp = currentlyAddingEvent) => {
    const newEvents = [...events, { timeStamp: timeStamp, text: eventText, index: 0, id: uuidv4() }].sort((a, b) => a.timeStamp > b.timeStamp).map((event, i) => ({ ...event, index: i + 1 }))

    setEvents(newEvents)
    console.log(newEvents)
    setCurrentlyAddingEvent(false)
  }

  const onFillColors = (color, i) => {
    const newColors = [...fillColors]
    newColors[i] = color.hex
    console.log(newColors)
    setFillColors(newColors)
  }

  const deleteEvent = (event) => {
    setEvents(events.filter(e => e.timeStamp !== event.timeStamp))
  }

  return (
    <Container>
      {/* <iframe id='pdf-file' />  Iframe used for pdf export debugging */}
      <PosterContainer>
        <AutoSize>
          <Poster title={title} subTitle={subTitle} symbol={stockSymbol.value} companyDescription={companyDescription} exchangeName={exchangeName} market={market} startDate={startDate} endDate={endDate} color={color} fill={fill ? fillColors : 'none'} eventColor={eventColor === '#000' ? eventColor : color} showDate={showDate} size={size} stockData={stockData} events={events} onAddEvent={setCurrentlyAddingEvent} lineThickness={lineThickness} />
        </AutoSize>
        {/* <Poster title={title} subTitle={subTitle} companyDescription={companyDescription} startDate={startDate} endDate={endDate} color={color} fill={fill ? fillColors : 'none'} eventColor={eventColor === '#000' ? eventColor : color} showDate={showDate} size={size} stockData={stockData} timeStamps={timeStamps} events={events} onAddEvent={setCurrentlyAddingEvent} lineThickness={lineThickness} /> */}

        {/* <PDFViewer dimensions={{ height: size.height, width: size.width }} aspectRatio={size.width / size.height}>
          <p>test</p>
        </PDFViewer> */}
      </PosterContainer>

      <SideBar>
        <StockSelect value={stockSymbol} onStockSelect={setStockSymbol} />

        <Input value={title} onChange={(e) => setTitle(e.target.value)} placeholder='Title' />
        <Input value={subTitle} onChange={(e) => setSubtitle(e.target.value)} placeholder='Subtitle' />

        <InputHeadline>Select start date</InputHeadline>
        <ContainedDatePicker
          format='dd/MM/yyyy'
          style={{ border: 'none' }}
          onChange={setStartDate}
          value={startDate}
        />
        <InputHeadline>Select end date</InputHeadline>
        <ContainedDatePicker
          format='dd/MM/yyyy'
          style={{ border: 'none' }}
          onCalendarClose={setEndDate}
          value={endDate}
        />

        <Select
          value={size}
          onChange={setSize}
          options={sizes}
        />

        <Select value={interval} onChange={setInterval} options={intervals} />

        <InputHeadline>Stroke thickness: {lineThickness}px</InputHeadline>
        <Slider
          axis='x'
          x={lineThickness * 10}
          onChange={({ x }) => setLineThickness(x / 10)}
        />

        <Swatch colors={color} />
        <TwitterPicker
          colors={['#000', '#B80000', '#DB3E00', '#FCCB00', '#008B02', '#006B76', '#1273DE', '#004DCF', '#5300EB', '#EB9694', '#FAD0C3', '#FEF3BD', '#C1E1C5', '#BEDADC', '#C4DEF6', '#BED3F3', '#D4C4FB']}
          width='100%'
          color={color}
          disableAlpha
          onChange={(color) => setColor(color.hex)}
        />

        <Checkbox value={fill} onChange={() => setFill(!fill)}>Filled</Checkbox>
        {fill && <GradientSelector fillColors={fillColors} onFillColors={(color, i) => onFillColors(color, i)} />}

        <Checkbox value={showDate} onChange={() => setShowDate(!showDate)}>Show date</Checkbox>
        <Checkbox value={eventColor === '#000'} onChange={() => setEventColor(eventColor === '#000' ? color : '#000')}>Black event indicators</Checkbox>

        <EventEditor events={events} onAdd={(event) => addEvent(event.text, event.timeStamp)} onDelete={deleteEvent} />

        <Button onClick={() => exportPDF(getPointsFromSize(size))}>Export PDF</Button>

      </SideBar>

      <InputPopup maxCharacters={46} visible={currentlyAddingEvent} onClose={() => setCurrentlyAddingEvent(false)} title={'Create event for ' + dateToString(new Date(currentlyAddingEvent * 1000))} onSubmit={addEvent} />
    </Container>
  )
}

export default App
