const getDiffMs = (startDate, endDate) => {
  const start = startDate.getTime()
  const end = endDate.getTime()
  return end - start
}

// One of the following is allowed 1m|2m|5m|15m|60m|1d
const getInterval = (startDate, endDate) => {
  const hours = Math.round(getDiffMs(startDate, endDate) / 1000) / 60 / 60
  if (hours <= 24) {
    return '5m'
  } else if (hours <= 24 * 5) {
    return '15m'
  } else if (hours <= 24 * 40) {
    return '60m'
  } else {
    return '1d'
  }
}

const zeroDate = (date) => {
  date.setHours(0)
  date.setMinutes(0)
  date.setSeconds(0)
}

export const getChart = async (symbol, interval, startDateIn, endDateIn) => {
  const startDate = new Date(startDateIn.getTime())
  const endDate = (endDateIn.getTime() > (new Date()).getTime()) ? new Date() : new Date(endDateIn.getTime())

  zeroDate(startDate)
  zeroDate(endDate)
  endDate.setDate(endDate.getDate() + 1)

  return new Promise(function (resolve, reject) {
    const req = unirest('GET', 'https://apidojo-yahoo-finance-v1.p.rapidapi.com/stock/v2/get-chart')
    console.log('interval: ' + getInterval(startDate, endDate))

    req.query({
      interval: getInterval(startDate, endDate),
      symbol: symbol,
      period2: Math.round(endDate.getTime() / 1000),
      period1: Math.round(startDate.getTime() / 1000)
      // range: range, // Allowed values are (1d | 5d | 3mo | 6mo | 1y | 5y | max)
      // region: 'US'
    })

    req.headers({
      'x-rapidapi-host': 'apidojo-yahoo-finance-v1.p.rapidapi.com',
      'x-rapidapi-key': '826524812emshaa5f49b4f980612p125332jsn1d475b70200c',
      useQueryString: true
    })

    req.end(function (res) {
      if (res.error) throw new Error(res.error)

      // console.log(res.body)
      const body = res.body
      console.log(body)
      const stockArray = []

      if (body.chart.result[0].timestamp) {
        for (const [i, v] of body.chart.result[0].timestamp.entries()) {
          // if (v * 1000 > startDate.getTime() && v * 1000 < endDate.getTime()) {

          //   stockArray.push({ timestamp: v, close: body.chart.result[0].indicators.quote[0].close[i] })
          // }
          stockArray.push({ timestamp: v, close: body.chart.result[0].indicators.quote[0].close[i] })
        }
      }

      // console.log(stockArray)
      window.localStorage.setItem('testData', JSON.stringify(body))
      resolve(stockArray)
    })
  })
}

