import React from 'react'
import { getIndexFromTimeStamp } from '../../lib/utils'

const lineLength = 10

function Event ({ event, stockData, points, color, fontSize = 7 }) { // These are some ugly props... But it makes it easy to use
  const index = getIndexFromTimeStamp(stockData, event.timeStamp)

  if (index === -1) {
    return ('')
  }

  return (
    <g transform={'translate(' + points[getIndexFromTimeStamp(stockData, event.timeStamp)].split(' ')[0] + ',' + points[getIndexFromTimeStamp(stockData, event.timeStamp)].split(' ')[1] + ')'}>
      <circle cx={0} cy={-lineLength} r='6' stroke='none' fill={color} />
      {/* strokeDasharray='5,5' */}
      <line x1={0} y1={0} x2={0} y2={-lineLength} stroke={color} strokeWidth={0.5} />
      <text fill='#fff' fontWeight='bolder' fontSize={fontSize} y={-lineLength + 0} x='0' textAnchor='middle' dy='.3em'>{event.index}</text>
    </g>
  )
}
export default Event
